import React, { useState } from 'react';
import * as FaIcons from "react-icons/fa";
import "./style.css";

export default function About() {
	const [sidebar, setSidebar] = useState(false);
	let currentYear = new Date().getFullYear();
	const str = "Copyright \u00A9 2019-" + currentYear + " Janzen Christian Aguila. All Rights Reserved."; // &copy; was initially used
	const showSidebar = () => setSidebar(!sidebar);

	const showSidebarText = () => {
		let text = document.getElementById("sidebarText");
		text.innerHTML = "Toggle sidebar";
	}

	const hideSidebarText = () => {
		let text = document.getElementById("sidebarText");
		text.innerHTML = " ";
	}

	return (
		<div id="aboutMain">
			<div>
				<div className="attach" id="sidebarToggle">
					<a href="#sidebarToggle"  onClick={showSidebar} onMouseOver={showSidebarText} onMouseOut={hideSidebarText}><FaIcons.FaBars /><span id="sidebarText"> </span></a>
				</div>
				<div className={sidebar ? "links hideShow hidden" : "links hideShow"}>
					<h4>Quick Links</h4>
					<hr />
					<a href="#logo">TextSimEngine</a><br />
					<a href="#aboutApp">About the app</a><br />
					<a href="#help">Need help?</a><br />
					<ol>
						<li><a href="#signup">Signing up</a></li>
						<li><a href="#login">Logging in</a></li>
						<li><a href="#create">Creating your game</a>
							<ul>
								<li><a href="#view">View item details</a></li>
								<li><a href="#add">Add item</a></li>
								<ul>
									<li><a href="#addTitle">Title</a></li>
									<li><a href="#addContent">Node content</a></li>
									<li><a href="#addSkip">Skip to</a></li>
									<li><a href="#addSound">Add Sound</a></li>
									<li><a href="#addVariables">Variables</a></li>
									<li><a href="#addConditions">Conditions</a></li>
								</ul>
								<li><a href="#delete">Delete item</a></li>
								<li><a href="#edit">Edit item</a></li>
								<li><a href="#deleteChild">Delete children</a></li>
								<li><a href="#transferNode">Transfer node</a></li>
								<li><a href="#expand">Expand all</a></li>
								<li><a href="#collapse">Collapse all</a></li>
							</ul>
						</li>
						<li><a href="#save">Saving your game</a></li>
						<li><a href="#export">Exporting your game</a></li>
						<li><a href="#embed">Embedding your game</a></li>
						<li><a href="#logout">Logging out</a></li>
					</ol>
					<a href="#important">IMPORTANT NOTES!</a><br />
					<a href="#acknowledgement">Acknowledgement</a><br />
					<a href="#aboutDev">About the developer</a><br />
					<a href="#end">End of page</a><br />
				</div>
			</div>
			<img src={require("../../Assets/InitLogo.png")} alt="TextSimEngine" id="logo" />
			<div id="aboutApp">
				<h1>About the app</h1>
				<hr width="50%" />
				<p className="regulatedWidth">
					TextSimEngine is an game engine that is used to create text-based games.<br /><br />

					This application gives you the power to turn your stories into interactive stories
					even without programming knowledge!<br /><br />

					Its goal is to help make development of text-based games easier for everyone with the
					implementation of a drag-and-drop graphical user interface and to make games accessible for
					everyone with embeddable versions of the games they created.
				</p>
			</div>
			<div>
				<h1 id="help">Need help?</h1>
				<hr width="50%" />
				<div className="regulatedWidth">
					<div id="gettingStarted" className="alignLeft">
						<span className="boldText">Getting started</span><br />
						<ol>
							<li><span id="signup" className="boldText">Signing up</span><br />
								Create your first game in less than a minute, and it's free! Are you wondering what the labels mean?
								<ul className="unstyledList">
									<li><span className="boldText">Alias</span>
										<p className="indented">This is what you use to login to your game. Somewhat similar to a username in other sites.</p>
									</li>
									<li><span className="boldText">Name</span>
										<p className="indented">This is your game's name in public upon export. This is basically your game's title. Get creative!</p>
									</li>
									<li><span className="boldText">Password</span>
										<p className="indented">Pretty self-explanatory. This is your password, which you use for restricting access to the game. Make sure to remember your password (since we don't offer a password recovery option yet).</p>
									</li>
								</ul>
								After filling up the fields, hit the "Submit" button and you're ready to make your first game!
							</li>
							<li><span id="login" className="boldText">Logging in:</span><br />
								If you already have a game and you want to edit it, just go to the login section of the start page and enter your game's alias and password.
							</li>
							<li>
								<span id="create" className="boldText">Creating your game:</span><br />
								You're finally ready to make some games!<br /><br />
								In the starting screen, you should see that there is an item called a "node" with the content "Edit this content". This is your root node. This can never be deleted, and can only be edited. You have to start somewhere!<br /><br />
								Right-click on the node to view the context menu. The options in the menu are described below.<br />
								<ul className="unstyledList">
									<li><span id="view" className="boldText">View item details</span><hr />
										<p className="indented">This shows all the attributes of the specific node you chose. It shows its title, name, ID, previous page, which node it skips to, child nodes, variables associated with it, and the conditions for those variables.<br />
										These will be explained further in the addition of new nodes on the next part.</p>
									</li>
									<li><span id="add" className="boldText">Add item</span><hr />
										<div className="indented">Upon clicking this option, it shows a window where you can specify what your newly added node will be.
											<div>
												<span id="addTitle" className="boldText">Title</span> - This is the title of your node. You can use it later to track your work and identify which node is which, especially when you specify skips for the nodes.<br />
												<span id="addContent" className="boldText">Node content</span> - This is your node's main content. This is the main message that will be displayed when your game is exported for playing. In short, this is where the bulk of your story goes! If you also want to include changing components to your game, like a player's name or an inventory of items check out the variables section of the guide!<br />
												<span id="addSkip" className="boldText">Skip to</span> - This is where you define if your node will skip to another node afterwards. These can only be applied on nodes that do not have child nodes, as the node they're skipping to will basically be their only "child".<br />
												<span id="addSound" className="boldText">Add Sound</span> - This is the section where you can add sounds to your current node. You can use any sound format (ie. mp3, wav) on the internet as long as they are publicly available or downloadable, and as long as you have the proper rights to use them either personally or commercially (ex. This sample sound from <a href="https://gamesounds.xyz/OpenBundle/Background%20Music%20and%20Loops/Elevator%20Music%20%28loop%29.mp3">Gamesounds</a>).<br />
												<span id="addVariables" className="boldText">Variables</span> - This is where you may add variables. Remember that it was said on the first part of this page that you don't need programming skills? Now, you'll get to learn a bit of it!<br /><br />
												What these variables do is add a bit of twist to your game. Say you want to greet a player by their name <s>but God said "you can't"</s>, this part gives you the option to greet the player hello!<br />
												You can also define things here that you want to change throughout the game (for example, a backpack that can store different items). Simply follow these syntax to learn how to manipulate variables!<br /><br />
												<p className="indented">
													<span className="boldText">Creator-defined variables</span> - these are variables you don't want the player to change, or at least the only ones that you the creator can change.<br />
													The syntax is: <i>(without the asterisks(*))</i><br /><code>*variable_name*=*variable_value*</code><br />
													For strings, use the syntax <i>(without the asterisks(*))</i>:<br /><code>*variable_name*="*variable_value*"</code><br />
													To put them inside your node's content, use this syntax: <i>(without the asterisks(*))</i><br />
													<code>{`{*variable_name*}`}</code><br /><br />
													<span className="boldText">Player-defined variables</span> - these are variables you give the player the option to change. These are basically user-inputs.<br />
													The syntax is:<i>(without the asterisks(*))</i><br /><code>*variable_name*=input</code><br />
													To put them inside your node's content, use this syntax: <i>(without the asterisks(*))</i><br />
													<code>{`{*variable_name*=input}`}</code><br /><br />
												</p>
												<span id="addConditions" className="boldText">Conditions</span> - This is hidden at first, but the "Add condition" will be available once you define your variables. These add more twists to your game, as it can use your variables to generate a skip to a different node or hide the specific node upon meeting the condition.<br />
												This is another bit of programming, and you will learn to make your game a lot more interactive. The specifics are explained below.<br /><br />
												<p className="indented">
													<span className="boldText"><i>if</i> ______</span> - This is where you define when your condition should trigger. Say for example, you defined a variable named <code>apples=10</code> and you decided to change the value throughout the game, and upon reaching 100 you want to do something, whether finishing the game, hiding a node, or something else. In here you just have to do <code>if apples=100</code> and change the other two components to your liking.<br /><br />
													<span className="boldText">Skip to</span> - This is where you define which node you want to go to if the given condition is achieved. Say you want to end the game, you choose the option that shows the title of the last page of the game.<br /><br />
													<span className="boldText">Hide choice</span> - This is where you hide the node you're currently working with. Say you have a variable named <code>money=100</code> but this node requires the player's <code>money</code> to be <code>1000</code>. You can then choose to tick this option to disallow them from accessing the next node.<br />
												</p>
											</div>
										</div>
									</li>
									<li><span id="delete" className="boldText">Delete item</span><hr />
										This is pretty self-explanatory, as clicking this node will give you a prompt to delete the current node. Take note that deleting a node also deletes its children! (Also, remember that the root node can never be deleted)<br /><br />
									</li>
									<li><span id="edit" className="boldText">Edit item</span><hr />
										If you followed how to add a new node, then you just basically apply it here as well since editing a node only changes what is already in an existing node.<br /><br />
									</li>
									<li><span id="deleteChild" className="boldText">Delete children</span><hr />
										This is almost the same as deleting a node, except it only deletes the node's children and keeps the current node.<br /><br />
									</li>
									<li><span id="transferNode" className="boldText">Transfer node</span><hr />
										This allows you to change a node's parent, and transfer it to another one. Just select the appropriate title of the respective node in the dropdown.<br /><br />
									</li>
									<li><span id="expand" className="boldText">Expand all</span><hr />
										Selecting this shows all the children of a certain node.<br /><br />
									</li>
									<li><span id="collapse" className="boldText">Collapse all</span><hr />
										Selecting this hides all the children of a certain node.<br /><br />
									</li>
								</ul>
							</li>
							<li><span id="save" className="boldText">Saving your game:</span><br />
								Once you think your game is good enough, or you just want to take a break for a bit you can save your game! Just open the hamburger menu (<FaIcons.FaBars />) on the top left corner and select "<FaIcons.FaSave /> Save Game" from the options. You will be alerted that your game has been saved, and now you can take a break or better yet, export and embed your game for everyone else to play!
							</li>
							<li><span id="export" className="boldText">Exporting your game:</span><br />
								Choosing this option means that you want to check how the actual game would look like. Once again, simply open the hamburger menu (<FaIcons.FaBars />) on the top left corner and select "<FaIcons.FaFileExport /> Export Game" from the options. This will generate a your game on the server, and once it's done then it will be ready for embedding onto your website!
							</li>
							<li><span id="embed" className="boldText">Embedding your game:</span><br />
								After the export process is done, you can finally share your game! You can choose to embed it on your website, or just play the game as is in all its glory. Take note though that you should export the game first for the embed to actually work.
							</li>
							<li><span id="logout" className="boldText">Logging out:</span><br />
								If you have already done what you need to do or discard your progress and revert it to its last saved state, you can select the logout option.
							</li>
						</ol>
						<hr />
					</div>
				</div>
			</div>
			<div>
				<h1 id="important">IMPORTANT NOTES!</h1>
				<hr width="50%" />
				<div className="regulatedWidth">
					<ol>
						<li>Again, make sure to remember your password as the app currently doesn't have any way of recovering forgotten passwords. Please <a className="boldText" href="mailto:jdaguila@up.edu.ph">send an email to the developer</a> if you have forgotten your password for help.</li>
						<li>Before saving your game, make sure that you have<span className="boldText">EXPANDED ALL</span> the nodes of your game, as the game engine only saves what is visible on the currently expanded tree.</li>
					</ol>
				</div>
			</div>
			<div className="spaced">
				<br />
				<br />
					<hr className="styled" width="75%" />
				<br />
				<br />
			</div>
			<div id="acknowledgement">
				<h1 id="important">Acknowledgement</h1>
				<hr width="50%" />
				<div className="regulatedWidth">
					I would like to thank Ma'am Katrina Joy Abriol-Santos, for always guiding me and for always giving me ideas for my special problem. I am very grateful for your assistance, and for the opportunity you have given me to create this application.<br /><br />
					I would also like to thank my fellow friends, especially my course mates here in the university who helped me as well. Without them, I would not have the motivation to finish this special problem. Thank you for your support and help throughout my study.<br /><br />
					Lastly, I would also like to express my gratitude to my parents who never got tired of encouraging me and providing me support while I was working on my special problem. Without their efforts, I would never have made it to where I am right now.<br /><br />
				</div>
			</div>
			<div id="aboutDev">
				<h1>About the developer</h1>
				<hr width="50%" />
				<div className="regulatedWidth">
					<span className="boldText">Janzen Christian D. Aguila</span> is an undergraduate BS
					Computer Science student at the University at the
					Philippines Los Baños. He is one of the developers
					for the Alliance of Computer Science Students - UPLB.
					He likes to explore web development and
					has worked on different projects involving the use
					of different web development frameworks.
					<hr width="50%" />
					This application is presented to the
					<div className="spaced">
						<a className="boldText largeText" href="http://ics.uplb.edu.ph/" target="_blank" rel="noopener noreferrer">Institute of Computer Science</a><br />
						<a href="https://cas.uplb.edu.ph/" target="_blank" rel="noopener noreferrer">College of Arts and Sciences</a><br />
						<a href="https://www.uplb.edu.ph/index.php/home" target="_blank" rel="noopener noreferrer">University of the Philippines Los Baños</a>
					</div>
					in partial fulfillment of the requirements for
						<div className="spaced">
							<h3>CMSC 190</h3>
							<h6>Special Problems</h6>
						</div>
				</div>
			</div>
			{str}<br />
			<span id="end">You have reached the end of the page.</span>
		</div>
	);
}