import React from 'react';
import { useParams } from "react-router-dom";

import { SERVER } from '../../config.js';

// 	import NotFound from '../../pages/NotFound/NotFound';

export default function Embed() {
	let { id } = useParams();
	let path = SERVER + "/exports/" + id + "/index.html";

	return (
		<div>
			<iframe id="htmlFrame" title="gameFrame" style={{"position": "relative", "top": "0", "left": "0", "width": "100%", "height": "100vh", "border": "0"}} src={ path }></iframe>
			<div style={{"position": "absolute", "bottom": "0", "margin": "0 auto", "zIndex": "100", "width": "100%", "textAlign": "center"}}>
				Made with <a href="/" target="_blank" rel="noopener noreferrer">TextSimEngine</a>
			</div>
		</div>
	);
}