import React, { useState } from 'react';
import * as FaIcons from "react-icons/fa";
import * as AiIcons from "react-icons/ai";
import * as FiIcons from "react-icons/fi";

import { Link } from "react-router-dom";
import "./Navbar.css";
import { IconContext } from "react-icons";

import Logout from '../pages/Logout/Logout';

function Navbar() {
	const [sidebar, setSidebar] = useState(false);

	const clickSaveGame = () => {
		document.getElementById("saveTreeLink").click();
	}

	const clickExportGame = () => {
		document.getElementById("exportGameLink").click();
	}

	const clickEmbedGame = () => {
		document.getElementById("embedGameLinkContainer").click();
	}

	const showSidebar = () => setSidebar(!sidebar);
	return (
		<div>
			<IconContext.Provider value={{color: "#fff"}}>
				<div className="navbar">
					<Link to="#" className="menu-bars">
						<FaIcons.FaBars onClick={showSidebar} />
					</Link>
					<Link to="/" className="menu-bars" style={{color:"#fff"}}>
						<img src={require("../Assets/InitLogo.png")} alt="TextSimEngine" style={{"maxHeight": "70px"}}/>
					</Link>
				</div>
				<nav className={sidebar ? "nav-menu active" : "nav-menu"}>
					<ul className="nav-menu-items" onClick={showSidebar}>
						<li className="navbar-toggle">
							<Link to="#" className="menu-bars">
								<AiIcons.AiOutlineClose />
							</Link>
						</li>
						<br />
						{/*{SidebarData.map((item, index) => {
							return (
								<li key={index} className={item.cName}>
									<Link to={item.path}>
										{item.icon}
										<span>{item.title}</span>
									</Link>
								</li>
							)
						})}*/}
						<li className="nav-text">
							<a href="/">
								<AiIcons.AiFillHome />
								<span>Home</span>
							</a>
						</li>
						<li className="nav-text">
							<a href="/#" onClick={clickSaveGame}>
								<FaIcons.FaSave />
								<span>Save Game</span>
							</a>
						</li>
						<li className="nav-text">
							<a href="/#" onClick={clickExportGame}>
								<FaIcons.FaFileExport />
								<span>Export Game</span>
							</a>
						</li>
						<li className="nav-text">
							<a href="/#" onClick={clickEmbedGame}>
								<FaIcons.FaExternalLinkAlt />
								<span>Embed Game</span>
							</a>
						</li>
						<li className="nav-text">
							<a id="helpPage" href="/about/engine#help" target="_blank" rel="noopener noreferrer">
								<FiIcons.FiHelpCircle />
								<span>Help</span>
							</a>
						</li>
						<li className="nav-text">
							<a href="/about/engine" target="_blank" rel="noopener noreferrer">
								<FaIcons.FaInfoCircle />
								<span>About app</span>
							</a>
						</li>
						<li className="nav-text">
							<a href="https://docs.google.com/forms/d/e/1FAIpQLSfks1Id_1n2hIk-4bo7pxs4BQ-xAI-nuqjue4GnY18ybI-s6A/viewform?usp=sf_link" target="_blank" rel="noopener noreferrer">
								<FaIcons.FaStar />
								<span>Rate the app</span>
							</a>
						</li>
						<li className="nav-text">
							<a href="mailto:jdaguila@up.edu.ph">
								<FiIcons.FiMail />
								<span>Contact</span>
							</a>
						</li>
						<hr />
						<li className="nav-text">
							<a href="/">
								<FiIcons.FiLogOut />
								<span><Logout /></span>
							</a>
						</li>
					</ul>
				</nav>
			</IconContext.Provider>
		</div>
	)
}

export default Navbar;