// import React, { Component } from 'react';
import React from 'react';
import {/* BrowserRouter, Link,*/ Route, Switch } from 'react-router-dom';

// import Questions from './Questions/Questions';
import GameInterface from './pages/GameInterface';
import Login from './pages/Login/Login';
// import Logout from './pages/Logout/Logout';
import Signup from './pages/Signup/Signup';
// import NavBar from './NavBar/NavBar';
import NotFound from './pages/NotFound/NotFound';
import Embed from './pages/Embed/Embed';
import About from './pages/About/About';
import useToken from './useToken';

import Navbar from './nav/Navbar';

function App() {
	const { token, setToken } = useToken();
	let gameNameMod = "/" + sessionStorage.getItem("gameName");

	if(!token) {
		let currentYear = new Date().getFullYear();
		const str = "Copyright \u00A9 2019-" + currentYear + " Janzen Christian Aguila. All Rights Reserved."; // &copy; was initially used

		return(
			<div>
				<Switch>
					<Route exact path="/:id">
						<Embed />
					</Route>
					<Route exact path="/about/engine">
						<About />
					</Route>
					<Route exact path="/">
						<div className="start">
							<div className="startItems" style={{"width": "75%", "margin": "auto", "textAlign": "center", "marginTop": "100px"}}>
								<div style={{"marginBottom": "50px"}}><img src={require("./Assets/InitLogo.png")} alt="TextSimEngine" style={{"maxWidth": "100%", "maxHeight": "100%"}} /></div>
								<div style={{"width": "100%", "display": "table"}}>
									<div style={{"display": "table-row"}}>
										<div className="fadeItems">
											<Signup setToken={setToken} />
										</div>
										&emsp;
										<div className="fadeItems">
											<Login setToken={setToken} />
										</div>
									</div>
								</div>
							</div>
							<div style={{bottom: "0", width: "100%", textAlign: "center", position: "absolute"}}>
								<a href="/about/engine" target="_blank" rel="noopener noreferrer" style={{color:"#FFF"}}>About TextSimEngine</a>&emsp;|&emsp;
								<a href="https://docs.google.com/forms/d/e/1FAIpQLSfks1Id_1n2hIk-4bo7pxs4BQ-xAI-nuqjue4GnY18ybI-s6A/viewform?usp=sf_link" target="_blank" rel="noopener noreferrer" style={{color:"#FFF"}}>Rate the app</a>&emsp;|&emsp;
								<a href="mailto:jdaguila@up.edu.ph" style={{color:"#FFF"}}>Contact the developer</a>
								<p style={{fontSize: "8px"}}>{str}</p>
							</div>
						</div>
					</Route>
				</Switch>
			</div>
		);
	}
	return(
		<div>
			<Switch>
				<Route exact path="/">
					<Navbar />
					<GameInterface />
					<a id="embedGameLink" href={ gameNameMod } target="_blank" rel="noopener noreferrer" style={{display:"none"}}> </a> {/*this is programatically clicked in Navbar.js*/}
				</Route>
				<Route exact path="/:id">
					<Embed />
				</Route>
				<Route exact path="/about/engine">
					<About />
				</Route>
				<Route component={ NotFound } />
			</Switch>
		</div>
	);
}

