import React from 'react';
import PropTypes from 'prop-types';
import axios from 'axios';
import * as AiIcons from "react-icons/ai";
import '../../style.css';


import { SERVER } from '../../config.js';

let newGameData = "";

async function loginUser(credentials) {
	return fetch(SERVER + '/game/login/', {
		method: 'POST',
		headers: {
			'Content-Type': 'application/json'
		},
		body: JSON.stringify(credentials)
	})
		.then(data => data.json());
}

async function createGame(credentials) {
	await axios.post(SERVER + '/game/createGame/', {
		name: credentials.name,
		alias: credentials.alias,
		password: credentials.password
	})
		.then(res => {
			if(res.data !== "Alias has been used.") {
				newGameData = {
					alias: credentials.alias,
					password: credentials.password
				}
				return newGameData;
			}
			else {
				newGameData = "";
				console.log(res.data);
			}
		})
		.catch((e) => {
			console.log("Failed to create game.")
		});
}

export default function Signup({ setToken }) {
	var name = "";
	var alias = "";
	var password = "";
	let pass = false;

	const setAlias = e => {
		let validityCheck = document.getElementById("validityCheck");
		validityCheck.innerHTML = "Checking alias availability...";
		alias = e;
		let temp_alias = alias;
		setTimeout(function () {
			if (temp_alias === alias) {
				console.log("The value hasn't changed.");
				axios.post(SERVER + '/game/checkAlias/', {
					alias: alias
				})
					.then(res => {
						console.log(res.data);
						if(res.data === "Reject") {
							validityCheck.innerHTML = "This alias is no longer available.";
							pass = false;
						}
						else {
							if(alias === "") {
								validityCheck.innerHTML = "This field is required.";
								pass = false;
							}
							else {
								validityCheck.innerHTML = "This alias is available!";
								pass = true;
							}
						}
					});
			}
			else {
				temp_alias = alias;
			}
		}, 1000);
	}

	const setName = e => {
		name = e;
	}

	const setPassword = e => {
		password = e;
	}

	const togglePasswordFunc = () => {
		const password = document.getElementById("authPwSignup");
		const type = password.getAttribute('type') === 'password' ? 'text' : 'password';
		password.setAttribute('type', type);
	}

	const login = async e => {
		document.getElementById("coverSignup").style.display = "block";
		document.getElementById("signupFormContainer").style.opacity = "0.25";
		const token = await loginUser({
			username: alias,
			password
		});
		if(token.token.substring(0,1) === "$") {
			sessionStorage.setItem("gameName", alias);
			setToken(token);
		}
		else {
			alert(token.token);
			document.getElementById("authAliasSignup").value = "";
			document.getElementById("authNameSignup").value = "";
			document.getElementById("authPwSignup").value = "";
			document.getElementById("coverSignup").style.display = "none";
			document.getElementById("signupFormContainer").style.opacity = "1";
		}
	}

	const handleSubmit = async e => {
		e.preventDefault(); // prevents submission of empty form fields
		if(name !== "" && alias !== "" && password !== "") {
			if(pass === true) {
				await createGame({
					name,
					alias,
					password
				})
				login(e);
			}
		}
		else {
			alert("Please fill out all the fields and check them for errors before submitting.")
		}
	}

	return (
		<div>
			<div id="coverSignup" style={{"width": "100%", "height": "100%", "z-index": "1", "textAlign":"center", "position": "fixed", "display": "none"}}>
				<h2 style={{"color": "#fff", "marginTop": "10%"}}>Creating...</h2>
			</div>
			<h4>Create a new game:</h4>
			<div id="signupFormContainer" style={{"width": "50%", "margin": "auto"}}>
				<form onSubmit={ handleSubmit }>
					<div className="help-tip">
						<p>This is the unique ID you assign to your game. You will use this for logging in to your game later on, so make sure to remember it.</p>
					</div>
					<label className="inp">
						<input id="authNameSignup" type="text" onChange={e => setAlias(e.target.value)} placeholder="&nbsp;" />&ensp;
						<span className="label">Alias</span>
						<span className="focus-bg"></span>
						<span id="validityCheck"></span>
					</label>
					<br />


					<div className="help-tip">
						<p>This is your game's display name. Be creative all you want and think of a good name!</p>
					</div>
					<label className="inp">
						<input id="authAliasSignup" type="text" onChange={e => setName(e.target.value)} placeholder="&nbsp;" />&ensp;
						<span className="label">Name</span>
						<span className="focus-bg"></span>
					</label>
					<br />

					<div className="help-tip">
						<p>Choose a secure password for your game so it can't be easily modified by others.</p>
					</div>
					<label className="inp">
						<input id="authPwSignup" type="password" onChange={e => setPassword(e.target.value)} placeholder="&nbsp;" />&ensp;
						<span className="label">Password</span>
						<span className="focus-bg"></span>
					</label>
					<span id="togglePasswordSignup" className="eye" onClick={togglePasswordFunc}><AiIcons.AiFillEye /></span>

					<div>
						<button type="submit" className="btn btn-sm animated-button thar-one">Submit</button>
					</div>
				</form>
			</div>
		</div>
	);
}

Signup.propTypes = {
	setToken: PropTypes.func.isRequired
};