import React, { useState } from 'react';
import PropTypes from 'prop-types';
import * as AiIcons from "react-icons/ai";
import '../../style.css';

import { SERVER } from '../../config.js';

async function loginUser(credentials) {
	return fetch(SERVER + '/game/login/', {
		method: 'POST',
		headers: {
			'Content-Type': 'application/json'
		},
		body: JSON.stringify(credentials)
	})
		.then(data => data.json());
}

export default function Login({ setToken }) {
	const [username, setUserName] = useState();
	const [password, setPassword] = useState();

	const togglePasswordFunc = () => {
		const password = document.getElementById("authPw");
		const type = password.getAttribute('type') === 'password' ? 'text' : 'password';
		password.setAttribute('type', type);
	}

	const handleSubmit = async e => {
		let aliasCheck = document.getElementById("aliasCheck");
		let passCheck = document.getElementById("passCheck");
		let check = false;
		e.preventDefault(); // prevents submission of empty form fields
		if(username === "" || username === undefined) {
			aliasCheck.innerHTML = "Username field must be filled out.";
			check = false;
		}
		if(password === "" || password === undefined) {
			passCheck.innerHTML = "Password field must be filled out.";
			check = false;
		}
		if(username !== "" && username !== undefined && password !== "" && password !== undefined) {
			aliasCheck.innerHTML = passCheck.innerHTML = "";
			check = true;
		}
		if(check === true) {
			document.getElementById("coverLogin").style.display = "block";
			document.getElementById("loginFormContainer").style.opacity = "0.25";
			const token = await loginUser({
				username,
				password
			});
			var authStr = username + password;
			if(token.tokenStr === authStr) {
				sessionStorage.setItem("gameName", username);
				let moddedToken = {token: token.token};
				setToken(moddedToken);
			}
			else {
				alert(token.token);
				document.getElementById("coverLogin").style.display = "none";
				document.getElementById("loginFormContainer").style.opacity = "1";
			}
		}
	}

	return (
		<div>
			<div id="coverLogin" style={{"width": "100%", "height": "100%", "z-index": "1", "textAlign":"center", "position": "fixed", "display": "none"}}>
				<h2 style={{"color": "#fff", "marginTop": "10%"}}>Logging in...</h2>
			</div>
			<h4>Login to an existing game:</h4>
			<div id="loginFormContainer" style={{"width": "100%", "margin": "auto"}}>
				<form onSubmit={ handleSubmit }>
					<div>
						<label className="inp">
							<input id="authAlias" type="text" onChange={e => setUserName(e.target.value)} placeholder="&nbsp;" />
							<span className="label">Alias</span>
							<span className="focus-bg"></span>
							<span id="aliasCheck"></span>
						</label>
					</div>
					<br />
					<div>
						<label className="inp">
							<input id="authPw" type="password" onChange={e => setPassword(e.target.value)} placeholder="&nbsp;" />
							<span className="label">Password</span>
							<span className="focus-bg"></span>
							<span id="passCheck"></span>
						</label>
						<span id="togglePassword" className="eye" onClick={togglePasswordFunc}><AiIcons.AiFillEye /></span>
					</div>
					<div>
						<button type="submit" className="btn btn-sm animated-button thar-one">Submit</button>
					</div>
				</form>
			</div>
		</div>
	);
}

Login.propTypes = {
	setToken: PropTypes.func.isRequired
};